.topbar {
  position: fixed;
  top: 0;
  left: 80px;
  right: 0;
  height: 70px;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2rem;
  z-index: 100;
  transition: left 0.3s ease;
}

.leftSection {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.pageTitle {
  font-size: 1.5rem;
  font-weight: bold;
  color: #1f2937;
}

.breadcrumb {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.875rem;
  color: #6b7280;
}

.breadcrumb a {
  color: #667eea;
  text-decoration: none;
  transition: color 0.2s;
}

.breadcrumb a:hover {
  color: #5568d3;
}

.separator {
  color: #d1d5db;
}

.rightSection {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.searchBar {
  position: relative;
  display: flex;
  align-items: center;
}

.searchInput {
  width: 300px;
  padding: 0.625rem 1rem 0.625rem 2.5rem;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  font-size: 0.875rem;
  background: white;
  transition: all 0.3s ease;
}

.searchInput:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  width: 350px;
}

.searchIcon {
  position: absolute;
  left: 0.75rem;
  width: 18px;
  height: 18px;
  color: #9ca3af;
}

.notifications {
  position: relative;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  background: white;
  border: 1px solid #e5e7eb;
  cursor: pointer;
  transition: all 0.2s;
}

.notifications:hover {
  border-color: #667eea;
  background: rgba(102, 126, 234, 0.05);
}

.notificationIcon {
  width: 20px;
  height: 20px;
  color: #6b7280;
}

.badge {
  position: absolute;
  top: -4px;
  right: -4px;
  width: 18px;
  height: 18px;
  background: #ef4444;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.625rem;
  color: white;
  font-weight: bold;
  border: 2px solid white;
}

.userMenu {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  background: white;
  border: 1px solid #e5e7eb;
  cursor: pointer;
  transition: all 0.2s;
}

.userMenu:hover {
  border-color: #667eea;
  background: rgba(102, 126, 234, 0.05);
}

.userAvatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.875rem;
  color: white;
  font-weight: bold;
}

.userInfo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.userName {
  font-size: 0.875rem;
  font-weight: 600;
  color: #1f2937;
}

.userStatus {
  font-size: 0.75rem;
  color: #10b981;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.statusDot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #10b981;
}

.chevron {
  width: 16px;
  height: 16px;
  color: #9ca3af;
  transition: transform 0.2s;
}

.userMenu:hover .chevron {
  transform: rotate(180deg);
}

@media (max-width: 768px) {
  .topbar {
    left: 80px;
    padding: 0 1rem;
  }

  .searchInput {
    width: 200px;
  }

  .searchInput:focus {
    width: 250px;
  }

  .userInfo {
    display: none;
  }
}
